import $ from "jquery";
import  { TimelineMax } from "gsap/all";

// Glitch Button
var $filter = document.querySelector('.svg-sprite'),
    $turb = $filter.querySelector('#filter feTurbulence'),
    turbVal = { val: 0.000001 },
    turbValX = { val: 0.000001 };

var glitchTimeline = function() {
    var timeline = new TimelineMax({
        repeat: -1,
        repeatDelay: 2,
        paused: true,
        onUpdate: function () {
            $turb.setAttribute('baseFrequency', turbVal.val + ' ' + turbValX.val);
        }
    });

    timeline
        .to(turbValX, 0.1, { val: 0.5 })
        .to(turbVal, 0.1, { val: 0.02 });
    timeline
        .set(turbValX, { val: 0.000001 })
        .set(turbVal, { val: 0.000001 });
    timeline
        .to(turbValX, 0.2, { val: 0.4 }, 0.4)
        .to(turbVal, 0.2, { val: 0.002 }, 0.4);
    timeline
        .set(turbValX, { val: 0.000001 })
        .set(turbVal, { val: 0.000001 });

    return {
        start: function() { timeline.play(0); },
        stop: function() { timeline.pause(); }
    };
};

var btnGlitch = new glitchTimeline();
$('.btn__glitch')
    .on('mouseenter', function () {
          $(this).addClass('btn__glitch--active');
          btnGlitch.start();
    })
    .on('mouseleave', function () {
        var $this = $(this);
        if ( $this.hasClass('btn__glitch--active') ) {
            $this.removeClass('btn__glitch--active');
            btnGlitch.stop();
        }
    });
    $('.section1__heading').addClass('btn__glitch--active');
     btnGlitch.start();

var textGlitch = new glitchTimeline();
$('.section1__greetings span').addClass('text--glitch');
$('.section4__desc span').addClass('text--glitch');
$('.section5__desc span').addClass('text--glitch');
textGlitch.start();


// Element Follow Cursor
function followCursor(e) {
    if (e.target.nodeName != 'IMG') return;

    e.target.style.opacity = '.6';

    let client_desc = e.target.nextElementSibling;
    client_desc.style.left = e.pageX + 'px';
    client_desc.style.top = e.pageY + 'px';
    client_desc.hidden = false;
}

function unfollowCursor(e) {
    if (e.target.nodeName != 'IMG') return;
    e.target.style.opacity = '1';
    let client_desc = e.target.nextElementSibling;
    client_desc.hidden = true;
}

const clients = document.querySelectorAll('.client');
clients.forEach(key => key.addEventListener('mousemove', followCursor));
clients.forEach(key => key.addEventListener('mouseout', unfollowCursor));



var words = ['Bea', 'UI Designer', 'UX Designer', 'Design Thinker', 'Problem Solver', 'and a Gamer', 'Player 1'],
    element = document.querySelector('.section1__intro span'),
    currentWord = -1;

window.setInterval(function(){
   currentWord++;
   if(currentWord >= words.length) currentWord = 0;
   element.textContent = words[currentWord];
}, 1000);